export default "/*****************************************************************\r\n * ReSpec 3 CSS\r\n * Robin Berjon - http://berjon.com/\r\n *****************************************************************/\r\n\r\n@keyframes pop {\r\n  0% {\r\n    transform: scale(1, 1);\r\n  }\r\n  25% {\r\n    transform: scale(1.25, 1.25);\r\n    opacity: 0.75;\r\n  }\r\n  100% {\r\n    transform: scale(1, 1);\r\n  }\r\n}\r\n\r\n/* Override code highlighter background */\r\n.hljs {\r\n  background: transparent !important;\r\n}\r\n\r\n/* --- INLINES --- */\r\nh1 abbr,\r\nh2 abbr,\r\nh3 abbr,\r\nh4 abbr,\r\nh5 abbr,\r\nh6 abbr,\r\na abbr {\r\n  border: none;\r\n}\r\n\r\ndfn {\r\n  font-weight: bold;\r\n}\r\n\r\na.internalDFN {\r\n  color: inherit;\r\n  border-bottom: 1px solid #99c;\r\n  text-decoration: none;\r\n}\r\n\r\na.externalDFN {\r\n  color: inherit;\r\n  border-bottom: 1px dotted #ccc;\r\n  text-decoration: none;\r\n}\r\n\r\na.bibref {\r\n  text-decoration: none;\r\n}\r\n\r\n.respec-offending-element:target {\r\n  animation: pop 0.25s ease-in-out 0s 1;\r\n}\r\n\r\n/* TODO: Remove once https://github.com/w3c/tr-design/pull/176 is merged. */\r\n.respec-offending-element a[href] {\r\n  margin: 0;\r\n}\r\n\r\n.respec-offending-element {\r\n  display: inline-block;\r\n  position: relative;\r\n  /* Red squiggly line */\r\n  background: url(data:image/gif;base64,R0lGODdhBAADAPEAANv///8AAP///wAAACwAAAAABAADAEACBZQjmIAFADs=)\r\n    bottom repeat-x;\r\n}\r\n@supports (text-decoration-style: wavy) {\r\n  .respec-offending-element {\r\n    background: none;\r\n    text-decoration-line: underline;\r\n    text-decoration-style: wavy;\r\n    text-decoration-color: red;\r\n  }\r\n}\r\n\r\n#references :target {\r\n  background: #eaf3ff;\r\n  animation: pop 0.4s ease-in-out 0s 1;\r\n}\r\n\r\ncite .bibref {\r\n  font-style: normal;\r\n}\r\n\r\ncode {\r\n  color: #c83500;\r\n}\r\n\r\nth code {\r\n  color: inherit;\r\n}\r\n\r\na[href].orcid {\r\n    padding-left: 4px;\r\n    padding-right: 4px;\r\n}\r\n\r\na[href].orcid > svg {\r\n    margin-bottom: -2px;\r\n}\r\n\r\n/* --- TOC --- */\r\n\r\n.toc a,\r\n.tof a {\r\n  text-decoration: none;\r\n}\r\n\r\na .secno,\r\na .figno {\r\n  color: #000;\r\n}\r\n\r\nul.tof,\r\nol.tof {\r\n  list-style: none outside none;\r\n}\r\n\r\n.caption {\r\n  margin-top: 0.5em;\r\n  font-style: italic;\r\n}\r\n\r\n/* --- TABLE --- */\r\n\r\ntable.simple {\r\n  border-spacing: 0;\r\n  border-collapse: collapse;\r\n  border-bottom: 3px solid #005a9c;\r\n}\r\n\r\n.simple th {\r\n  background: #005a9c;\r\n  color: #fff;\r\n  padding: 3px 5px;\r\n  text-align: left;\r\n}\r\n\r\n.simple th[scope=\"row\"] {\r\n  background: inherit;\r\n  color: inherit;\r\n  border-top: 1px solid #ddd;\r\n}\r\n\r\n.simple td {\r\n  padding: 3px 10px;\r\n  border-top: 1px solid #ddd;\r\n}\r\n\r\n.simple tr:nth-child(even) {\r\n  background: #f0f6ff;\r\n}\r\n\r\n/* --- DL --- */\r\n\r\n.section dd > p:first-child {\r\n  margin-top: 0;\r\n}\r\n\r\n.section dd > p:last-child {\r\n  margin-bottom: 0;\r\n}\r\n\r\n.section dd {\r\n  margin-bottom: 1em;\r\n}\r\n\r\n.section dl.attrs dd,\r\n.section dl.eldef dd {\r\n  margin-bottom: 0;\r\n}\r\n\r\n#issue-summary > ul,\r\n.respec-dfn-list {\r\n  column-count: 2;\r\n}\r\n\r\n#issue-summary li,\r\n.respec-dfn-list li {\r\n  list-style: none;\r\n}\r\n\r\ndetails.respec-tests-details {\r\n  margin-left: 1em;\r\n  display: inline-block;\r\n  vertical-align: top;\r\n}\r\n\r\ndetails.respec-tests-details > * {\r\n  padding-right: 2em;\r\n}\r\n\r\ndetails.respec-tests-details[open] {\r\n  z-index: 999999;\r\n  position: absolute;\r\n  border: thin solid #cad3e2;\r\n  border-radius: 0.3em;\r\n  background-color: white;\r\n  padding-bottom: 0.5em;\r\n}\r\n\r\ndetails.respec-tests-details[open] > summary {\r\n  border-bottom: thin solid #cad3e2;\r\n  padding-left: 1em;\r\n  margin-bottom: 1em;\r\n  line-height: 2em;\r\n}\r\n\r\ndetails.respec-tests-details > ul {\r\n  width: 100%;\r\n  margin-top: -0.3em;\r\n}\r\n\r\ndetails.respec-tests-details > li {\r\n  padding-left: 1em;\r\n}\r\n\r\na[href].self-link:hover {\r\n  opacity: 1;\r\n  text-decoration: none;\r\n  background-color: transparent;\r\n}\r\n\r\nh2,\r\nh3,\r\nh4,\r\nh5,\r\nh6 {\r\n  position: relative;\r\n}\r\n\r\naside.example .marker > a.self-link {\r\n  color: inherit;\r\n}\r\n\r\nh2 > a.self-link,\r\nh3 > a.self-link,\r\nh4 > a.self-link,\r\nh5 > a.self-link,\r\nh6 > a.self-link {\r\n  border: none;\r\n  color: inherit;\r\n  font-size: 83%;\r\n  height: 2em;\r\n  left: -1.6em;\r\n  opacity: 0.5;\r\n  position: absolute;\r\n  text-align: center;\r\n  text-decoration: none;\r\n  top: 0;\r\n  transition: opacity 0.2s;\r\n  width: 2em;\r\n}\r\n\r\nh2 > a.self-link::before,\r\nh3 > a.self-link::before,\r\nh4 > a.self-link::before,\r\nh5 > a.self-link::before,\r\nh6 > a.self-link::before {\r\n  content: \"\";\r\n  display: block;\r\n}\r\n\r\n@media (max-width: 767px) {\r\n  dd {\r\n    margin-left: 0;\r\n  }\r\n\r\n  /* Don't position self-link in headings off-screen */\r\n  h2 > a.self-link,\r\n  h3 > a.self-link,\r\n  h4 > a.self-link,\r\n  h5 > a.self-link,\r\n  h6 > a.self-link {\r\n    left: auto;\r\n    top: auto;\r\n  }\r\n}\r\n\r\n@media print {\r\n  .removeOnSave {\r\n    display: none;\r\n  }\r\n}\r\n"